.GitHub {
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.GitHubBody {
    display: none;
    margin-top: 10%;
    text-align: center;
}

.GitHubBody p{
    font-size: 21px;
    background-image:-webkit-gradient(linear, right top, left top, from(rgb(152, 162, 171)),to(#09afee));
    background-image:-webkit-linear-gradient(right, rgb(152, 162, 171),#09afee);
    background-image:-o-linear-gradient(right, rgb(152, 162, 171),#09afee);
    background-image:linear-gradient(to left, rgb(152, 162, 171),#09afee);
    background-image:-webkit-linear-gradient(left, rgb(152, 162, 171),#09afee);
    -webkit-background-clip: text;
    background-size: 200% 100%;
    -webkit-animation: github 4s infinite cubic-bezier(0, 0.01, 0.32, 1.28);
            animation: github 4s infinite cubic-bezier(0, 0.01, 0.32, 1.28);
    font-weight: bold;
    -webkit-text-fill-color: transparent;
}
@-webkit-keyframes github {
    0%  { background-position: 0 0;}
    100% { background-position: -100% 0;}
}


