.FindMusic {
    background-color: rgb(250, 250, 250);
    height: 100%;
    padding: 13px 30px 0 30px;
}

.FindMusic_header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 14px;
    border-bottom: 1px solid rgb(225, 225, 226);
}

.FindMusic_header ul {
    width: 505px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.FindMusic_header ul li {
    width: 56px;
    height: 28px;
    display: inline-block;
    margin-bottom: -1px;
    cursor: pointer;
    text-align: center;
}

.FindMusic_header ul li:hover {
    color: rgb(198, 47, 47);
}

.FindMusic_header ul li:nth-child(4) {
    color: rgb(198, 47, 47);
    border-bottom: 2px solid rgb(198, 47, 47);
}

.billboard {
    margin-top: 30px;
}

.billboard_title {
    font-size: 18px;
    font-family: "微软雅黑 Light";
    margin-top: 30px;
    padding: 0 0 10px 0;
    border-bottom: 1px solid rgb(225, 225, 226);
}

.billboard_list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;

}

.billboard_list > div {
    display: inline-block;
    border: 1px solid rgb(237, 237, 237);
    width: 244px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.billboard_list_title {
    height: 90px;
    background: -webkit-gradient(linear, left top, right top, from(rgb(61, 142, 222)), color-stop(1%, rgb(88, 162, 229)), color-stop(rgb(76, 143, 226)), to(rgb(91, 142, 233)));
    background: -webkit-linear-gradient(left, rgb(61, 142, 222), rgb(88, 162, 229) 1%, rgb(76, 143, 226), rgb(91, 142, 233));
    background: -o-linear-gradient(left, rgb(61, 142, 222), rgb(88, 162, 229) 1%, rgb(76, 143, 226), rgb(91, 142, 233));
    background: linear-gradient(to right, rgb(61, 142, 222), rgb(88, 162, 229) 1%, rgb(76, 143, 226), rgb(91, 142, 233));
    position: relative;
}

.billboard_list_title1_img {
    margin-left: 13px;
}

.billboard_list_play {
    cursor: pointer;
    border-radius: 100%;
    position: absolute;
    right: 15px;
    top: 50%;
    margin-top: -20px;
    opacity: 0.6;
}

.billboard_list_play:hover {
    opacity: 1;
}

.billboard_list_body_ul li {
    font-size: 12px;
    line-height: 14px;
    padding: 7px 9px 7px 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.billboard_list_body_ul span {
    cursor: default;
}

.billboard_list_body_ul span:nth-of-type(1) {
    vertical-align: middle;
    font-size: 16px;
    color: rgb(102, 102, 102);
}

.billboard_list_body_ul li:nth-of-type(odd) {
    background-color: rgb(245, 245, 247);
}

.billboard_list_body_ul li:hover {
    background-color: rgb(235, 236, 237);
}

.billboard_list_body_ul li:nth-child(-n+3) > span:first-of-type {
    color: rgb(205, 41, 41);
}

.billboard_list_body_ul span:nth-of-type(2) {
    width: 30px;
    font-size: 12px;
    text-align: left;
    color: rgb(136, 136, 136);
    -webkit-transform: scale(0.9);
    margin-left: 2px;
    display: inline-block;
    vertical-align: middle;
}

.billboard_list_body_ul span:nth-of-type(3) {
    margin-left: 5px;
    display: inline-block;
    width: 117px;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.billboard_list_body_ul span:nth-of-type(3) span {
    color: rgb(153, 153, 153);
    font-size: 12px;
    margin-left: 3px;
    vertical-align: baseline;
}

.billboard_list_body_ul span:nth-of-type(4) {
    width: 55px;
    text-align: right;
    vertical-align: middle;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    float: right;
    color: rgb(153, 153, 153);
}

.New {
    width: 32%;
}

.choice {
    background-color: rgb(227, 227, 229) !important;
}

.billboard {
    margin-top: 27px;
}

.billboard_title {
    font-size: 18px;
    font-family: "微软雅黑 Light";
    margin-top: 0;
    margin-left: 1px;
    padding: 0 0 7px 0;
    border-bottom: 1px solid rgb(225, 225, 226);
}

.billboard_list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 20px;
}

.billboard_list > div,.billboard_list2>div {
    display: inline-block;
    border: 1px solid rgb(237, 237, 237);
    width: 244px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.billboard_list2 {
    margin-bottom: 20px;
}
.billboard_list2 >div:nth-of-type(1){
    margin-right: 12px;
}


